import { IStorageAdapter, IStorageOptions, IStorageUsage } from '@file-chunk-uploader/types';

/**
 * IndexedDB
 * IndexedDB
 */
export class IndexedDBAdapter implements IStorageAdapter {
  private dbName: string;
  private storeName: string;
  private version: number;
  private keyPrefix: string;
  private db: IDBDatabase | null = null;
  private ready: Promise<void>;

  /**
   * IndexedDB
   * @param options 
   */
  constructor(options: IStorageOptions = {}) {
    this.dbName = options.dbName || 'file-chunk-uploader';
    this.storeName = options.storeName || 'uploads';
    this.version = options.version || 1;
    this.keyPrefix = options.keyPrefix || '';
    this.ready = this.initDB();
  }

  /**
   * IndexedDB
   * @returns Promise
   */
  private async initDB(): Promise<void> {
    return new Promise((resolve, reject) => {
      // IndexedDB
      if (!this.isSupported()) {
        reject(new Error('IndexedDB'));
        return;
      }

      const request = indexedDB.open(this.dbName, this.version);

      request.onerror = () => {
        console.error('IndexedDB');
        reject(new Error('IndexedDB'));
      };

      request.onsuccess = () => {
        this.db = request.result;
        resolve();
      };

      request.onupgradeneeded = () => {
        const db = request.result;

        // 
        if (!db.objectStoreNames.contains(this.storeName)) {
          const store = db.createObjectStore(this.storeName, { keyPath: 'id' });
          // 
          store.createIndex('fileId', 'fileId', { unique: false });
          store.createIndex('createdAt', 'createdAt', { unique: false });
          store.createIndex('type', 'type', { unique: false });
        }
      };
    });
  }

  /**
   * IndexedDB
   * @param key 
   * @param value 
   * @param expiration ()
   */
  async save<T>(key: string, value: T, expiration?: number): Promise<void> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readwrite');
        const store = transaction.objectStore(this.storeName);

        // 
        const prefixedKey = this.getKeyWithPrefix(key);
        const now = Date.now();
        const data = {
          id: prefixedKey,
          key: prefixedKey,
          value,
          createdAt: now,
          updatedAt: now,
          expireAt: expiration ? now + expiration : undefined,
          fileId: prefixedKey.split('_')[1], // key "type_fileId_extra"
          type: prefixedKey.split('_')[0], //  "state""chunk" 
        };

        // 
        const request = store.put(data);

        request.onsuccess = () => resolve();
        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * IndexedDB
   * @param key 
   * @returns null()
   */
  async get<T>(key: string): Promise<T | null> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readonly');
        const store = transaction.objectStore(this.storeName);
        const prefixedKey = this.getKeyWithPrefix(key);
        const request = store.get(prefixedKey);

        request.onsuccess = () => {
          const result = request.result;

          // 
          if (!result) {
            resolve(null);
            return;
          }

          // 
          if (result.expireAt && result.expireAt < Date.now()) {
            // 
            this.remove(key).catch(() => {
              // 
            });
            resolve(null);
            return;
          }

          resolve(result.value);
        };

        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * IndexedDB
   * @param key 
   */
  async remove(key: string): Promise<void> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const prefixedKey = this.getKeyWithPrefix(key);
        const request = store.delete(prefixedKey);

        request.onsuccess = () => resolve();
        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * IndexedDB
   * @param key 
   * @returns 
   */
  async has(key: string): Promise<boolean> {
    try {
      const value = await this.get(key);
      return value !== null;
    } catch (error) {
      return false;
    }
  }

  /**
   * IndexedDB
   */
  async clear(): Promise<void> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const request = store.clear();

        request.onsuccess = () => resolve();
        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * 
   * @returns 
   */
  async keys(): Promise<string[]> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readonly');
        const store = transaction.objectStore(this.storeName);
        const request = store.getAllKeys();

        request.onsuccess = () => {
          const results = Array.from(request.result as IDBValidKey[])
            .map(key => key.toString())
            .filter(key => key.startsWith(this.keyPrefix))
            .map(key => this.removePrefix(key));

          resolve(results);
        };

        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * 
   * @returns 
   */
  async getUsage(): Promise<IStorageUsage> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readonly');
        const store = transaction.objectStore(this.storeName);
        const request = store.getAll();

        request.onsuccess = () => {
          const results = request.result;
          let totalSize = 0;
          let chunkCount = 0;
          const fileIds = new Set<string>();

          results.forEach(item => {
            // 
            const itemSize = this.estimateSize(item);
            totalSize += itemSize;

            // 
            if (item.type === 'chunk') {
              chunkCount++;
            }

            // ID
            if (item.fileId) {
              fileIds.add(item.fileId);
            }
          });

          resolve({
            totalSize,
            chunkCount,
            fileCount: fileIds.size,
          });
        };

        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * 
   */
  async clearExpired(): Promise<void> {
    await this.ready;

    return new Promise((resolve, reject) => {
      if (!this.db) {
        reject(new Error(''));
        return;
      }

      try {
        const transaction = this.db.transaction([this.storeName], 'readwrite');
        const store = transaction.objectStore(this.storeName);
        const request = store.openCursor();

        const now = Date.now();

        request.onsuccess = event => {
          const cursor = (event.target as IDBRequest).result;

          if (cursor) {
            const data = cursor.value;

            // 
            if (data.expireAt && data.expireAt < now) {
              cursor.delete();
            }

            cursor.continue();
          } else {
            // 
            resolve();
          }
        };

        request.onerror = () => reject(new Error(''));
      } catch (err) {
        reject(err);
      }
    });
  }

  /**
   * IndexedDB
   * @returns 
   */
  isSupported(): boolean {
    return typeof indexedDB !== 'undefined';
  }

  /**
   * 
   * @param key 
   * @returns 
   */
  private getKeyWithPrefix(key: string): string {
    if (this.keyPrefix && !key.startsWith(this.keyPrefix)) {
      return `${this.keyPrefix}${key}`;
    }
    return key;
  }

  /**
   * 
   * @param key 
   * @returns 
   */
  private removePrefix(key: string): string {
    if (this.keyPrefix && key.startsWith(this.keyPrefix)) {
      return key.substring(this.keyPrefix.length);
    }
    return key;
  }

  /**
   * 
   * @param obj 
   * @returns 
   */
  private estimateSize(obj: any): number {
    if (obj === null || obj === undefined) return 0;

    // BlobFile
    if (obj instanceof Blob || obj instanceof File) {
      return obj.size;
    }

    // ArrayBuffer
    if (obj instanceof ArrayBuffer) {
      return obj.byteLength;
    }

    if (
      obj instanceof Int8Array ||
      obj instanceof Uint8Array ||
      obj instanceof Uint8ClampedArray ||
      obj instanceof Int16Array ||
      obj instanceof Uint16Array ||
      obj instanceof Int32Array ||
      obj instanceof Uint32Array ||
      obj instanceof Float32Array ||
      obj instanceof Float64Array
    ) {
      return obj.byteLength;
    }

    // UTF-8
    if (typeof obj === 'string') {
      // TextEncoderUTF-8
      if (typeof TextEncoder !== 'undefined') {
        return new TextEncoder().encode(obj).length;
      }
      // UTF-8
      // ASCII12-4
      let size = 0;
      for (let i = 0; i < obj.length; i++) {
        const code = obj.charCodeAt(i);
        if (code <= 0x7f) {
          size += 1; // ASCII
        } else if (code <= 0x7ff) {
          size += 2; // 
        } else if (code >= 0xd800 && code <= 0xdfff) {
          // UTF-16
          size += 4;
          i++; // 
        } else {
          size += 3; // 
        }
      }
      return size;
    }

    // 
    if (typeof obj === 'number') return 8;
    if (typeof obj === 'boolean') return 4;

    // 
    if (obj instanceof Date) return 8;

    // 
    if (typeof obj === 'object') {
      let size = 0;

      for (const key in obj) {
        if (Object.prototype.hasOwnProperty.call(obj, key)) {
          // 
          size += key.length * 2;
          // 
          size += this.estimateSize(obj[key]);
        }
      }

      return size;
    }

    return 0;
  }
}
