/**
 * 
 */
import {
  IUploadProgress,
  UploadStatus,
  ILogger,
  IStorageOptions,
  IUploadState,
  IUploadConfig,
} from '@file-chunk-uploader/types';

/**
 * 
 * 
 */
export enum ChunkStatus {
  /**  */
  PENDING = 'pending',
  /**  */
  UPLOADING = 'uploading',
  /**  */
  SUCCESS = 'success',
  /**  */
  FAILED = 'failed',
  /**  */
  PAUSED = 'paused',
}

/**
 * 
 */
export interface IChunkDetail {
  /**  */
  index: number;
  /**  */
  status: ChunkStatus;
  /**  */
  retryCount: number;
  /**  */
  lastAttempt?: number;
  /**  */
  lastError?: string;
}

/**
 * 
 */
export interface IResumeUploadStrategyOptions {
  /**  */
  storage?: IStorageOptions;
  /**  */
  enabled?: boolean;
  /**  */
  maxStorageTime?: number;
  /**  */
  maxConcurrentChunks?: number;
  /**  */
  visualizationCallback?: (fileId: string, chunksInfo: IChunkDetail[]) => void;
  /**  */
  cleanupInterval?: number;
  /**  */
  logger?: ILogger;
}

/**
 * 
 */
export interface IExtendedUploadState extends IUploadState {
  /** ID */
  fileId: string;
  /**  */
  fileName: string;
  /**  */
  fileSize: number;
  /**  */
  lastModified: number;
  /**  */
  uploadedChunks: number[];
  /**  */
  totalChunks: number;
  /**  */
  chunkSize: number;
  /**  */
  progress: IUploadProgress;
  /**  */
  status: UploadStatus;
  /**  */
  lastUpdated: number;
  /**  */
  config?: IUploadConfig;
  /**  */
  chunksDetails: IChunkDetail[];
  /**  */
  maxConcurrentChunks?: number;
  /**  */
  customData?: Record<string, any>;
}

/**
 * 
 */
export interface IUploadStats {
  /**  */
  total: number;
  /**  */
  uploaded: number;
  /**  */
  failed: number;
  /**  */
  pending: number;
  /**  */
  uploading: number;
  /**  */
  progress: number;
  /**  */
  estimatedTimeRemaining?: number;
}

/**
 * 
 */
export interface IUploadStateValidationResult {
  /**  */
  valid: boolean;
  /**  */
  reason?: string;
  /**  */
  recoverable?: boolean;
  /**  */
  details?: Record<string, any>;
}
