/**
 * 
 */

/**
 * 
 */
export enum LogLevel {
  DEBUG = 0,
  INFO = 1,
  WARN = 2,
  ERROR = 3,
  SILENT = 4,
}

/**
 * 
 */
export type LogCategory =
  | 'core'
  | 'event'
  | 'plugin'
  | 'network'
  | 'storage'
  | 'upload'
  | 'chunk'
  | 'resume'
  | 'performance'
  | string;

/**
 * 
 */
export type LogFormatter = (
  level: LogLevel,
  category: LogCategory,
  message: string,
  data?: any,
  timestamp?: number,
) => string;

/**
 * 
 */
export type LogOutputTarget = (formattedLog: string, rawLogData: ILogData) => void;

/**
 * 
 */
export interface ILogData {
  level: LogLevel;
  category: LogCategory;
  message: string;
  data?: any;
  timestamp: number;
}

/**
 * 
 */
export interface ILoggerConfig {
  level: LogLevel;
  formatter?: LogFormatter;
  outputs?: LogOutputTarget[];
  enabledCategories?: LogCategory[] | true;
  timestampFormat?: string;
  colorize?: boolean;
}

/**
 * 
 */
export interface IPluginTraceEvent {
  pluginId: string;
  hookName: string;
  startTime: number;
  endTime?: number;
  duration?: number;
  error?: Error;
  data?: any;
}

/**
 * 
 */
export interface IPluginTracerConfig {
  enabled: boolean;
  traceLimit?: number;
  logHookEvents?: boolean;
  performanceThreshold?: number;
}

/**
 * 
 */
export interface IDeveloperModeConfig {
  enabled: boolean;
  logger?: ILoggerConfig;
  pluginTracer?: IPluginTracerConfig;
  consoleApiEnabled?: boolean;
  performanceTracking?: {
    enabled: boolean;
    sampleRate: number;
    trackMemory: boolean;
  };
  breakOnError?: boolean;
}
